.App {
  display: grid;
  grid-template-columns: 1fr 3.75fr;
  grid-template-rows: 1fr 7fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.raceDisplay {
  color: azure;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.raceFactsDisplay {
  color:black;
  grid-area: 1 / 1 / 6 / 3;
}
.resultsDisplay {
  grid-area: 1 / 3 / 6 / 6;
}
.title {
  text-align: center;
  grid-area: 1 / 1 / 2 / 3;
  background-color: black;
  color: azure;
}
.list {
  text-align: left;
  grid-area: 2 / 1 / 3 / 2;
  background-color: black;
  color: azure;
}
.listedRace {
  cursor: pointer;
  width: 100%;
  text-align: left;
}
.race {
  grid-area: 2 / 2 / 3 / 3;
  background-color: azure;
  color: black;
  text-align: center;
}
a {
  color: navy;
}
a:hover {
  color: maroon;
}
#resultTable {
  margin-left: auto;
  margin-right: auto;
  width: 95%;
}
table,
tr,
th {
  border: 1px solid black;
  border-collapse: collapse;
}
